.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
		.container {
			padding: 0;
		}
	}
	.elements-wrapper-absolute {
		overflow: hidden;
		border-radius: 500px;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
	}
	.rsvp-wrapper {
		position: relative;
		padding: calc(100% - 10em) 2em 2em;
		@include max-md {
			padding: calc(100% - 8em) 2em 2em;
		}
		.image-wrapper {
			position: relative;
			width: 100%;
			aspect-ratio: 3/4;
			.rsvp-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 500px;
			}
		}
		.rsvp-form {
			position: relative;
			padding: 0 2rem;
			margin-bottom: 2rem;
			@include max-md {
				padding: 0 1rem;
			}
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.8);
			}
		}
	}
}
