.venue-section {
	position: relative;
	background-color: var(--color-primary);
	color: var(--color-dark);
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 50%;
		width: 100%;
		transform: translate(-50%, 0);
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
		}
		.venue-content {
			position: relative;
			@include max-md {
				.container {
					padding: 0;
				}
			}
			.ribbon-venue {
				position: relative;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-button-trans);
					}
				}
				.st0 {
					fill: var(--color-button-trans);
				}
				.st1 {
					fill: none;
					stroke: var(--color-button-trans);
					stroke-width: 19;
					stroke-linecap: round;
					stroke-miterlimit: 10;
					transform-origin: 50% 75%;
				}
			}
			.event-title {
				position: relative;
				font-family: $font-secondary;
				text-align: center;
			}
			.card {
				background-color: transparent;
				text-align: center;
				overflow: hidden;
				border: none;
				.card-body {
					position: relative;
					padding: 0;
					h4 {
						padding: 1rem 0;
					}
					.event-name {
						position: relative;
						background-color: var(--color-primary);
						width: 100%;
						padding: calc(100% - 10em) 1rem 3rem;
						overflow: hidden;
						@include max-lg {
							padding: calc(100% - 8em) 1rem 3rem;
						}
						&-body {
							position: relative;
						}
						.date {
							position: relative;
							width: 100%;
							display: flex;
							flex-direction: column;
							justify-content: center;
							align-items: center;
							h5 {
								font-family: $font-secondary;
								font-weight: 700;
								margin-bottom: 0;
								padding: 0.75rem;
								font-size: 28px;
								min-width: 30%;
							}
						}
						h4 {
							font-family: $font-secondary;
						}
						p {
							margin-bottom: 0;
							font-weight: 600;
						}
					}
					.event-description {
						position: relative;
						padding: 1rem 0;
					}
					.event-place {
						position: relative;
						background-color: var(--color-main);
						color: var(--color-dark);
						border-radius: var(--border-radius);
						padding: 1rem 1rem 2.5rem;
						&-body {
							position: relative;
							padding: 1rem;
							.elements-wrapper-absolute {
								overflow: hidden;
								img {
									border-radius: var(--border-radius);
								}
							}
							.widget-elements {
								position: absolute;
								bottom: 0;
								left: 50%;
								transform: translate(-50%, 50%);
							}
						}
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		row-gap: 3rem;
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
