.reminder-wrap {
	position: relative;
	display: block;
	padding: 3rem 0 0;
	.reminder-content {
		position: relative;
		background-color: var(--color-main);
		color: var(--color-dark);
		border-radius: var(--border-radius);
		padding: 1rem 1rem 2rem;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto;
		z-index: 2;
		.image-wrapper {
			position: relative;
			width: 100%;
			aspect-ratio: 6/5;
			.reminder-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: var(--border-radius);
			}
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
