.thank-section {
	position: relative;
	.orn-99 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 110%;
		height: 130%;
		transform: translate(-50%, 0);
		@include max-md {
			width: 115%;
			height: 120%;
		}
		.image-element {
			height: 100%;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: 50% 20%;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		padding: 5rem 0;
		overflow: hidden;
		@include max-md {
			padding: 3rem 0;
		}
		.thank {
			position: relative;
			min-height: 50vh;
			display: flex;
			align-items: center;
			justify-content: center;
			text-align: center;
			&-body {
				background: radial-gradient(ellipse, rgba(255, 255, 255, 0.6) 0%, transparent 80%);
				border-radius: 500px;
				padding: 3rem 2rem;
				@include max-md {
					padding: 2rem 1rem 5rem;
				}
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
